.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /**background-color: #282c34;**/
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(236, 229, 229);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ExpenseList {
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #ccc;
}

.ExpenseList h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.ExpenseItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
}

.ExpenseItem span {
  font-size: 18px;
  font-weight: bold;
}

.ExpenseItem button {
  padding: 5px 10px;
  background-color: #dc3545;
  color: #cf9898;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
